*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}
:root{
  --color: rgba(255, 255, 255, 0.562);
}
.card {
  background: #ffffff18;
  color: white;
  border-radius: 20px;
  padding: 30px;
}
.card h1{
  font-size: 20px;
}
.card p{
  font-size: 16px;
}
.card .btn{
padding: 10px 15px;
background: #0a0a0a40;
text-decoration: none;
color: white;
max-width: 100%;
border-radius: 8px;
border: 1px solid rgba(255, 255, 255, 0.356);
font-size: 18px;
display: flex;
align-items: center;
}
.card .btn span{
  margin-left: 8px;
}
.mt-16{
  margin-top: 16px;
}
.text-center{
text-align: center;
}

.profile .avatar{
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.678);
  width: 100px;
  height: 100px;
}
.profile .avatar:hover{
  width: 110px;
  height: 110px;
  border: 3px solid rgba(255, 166, 0, 0.76);
  transition: cubic-bezier(0.165, 0.84, 0.44, 1);
}
.card .btn:hover{
  background: rgba(255, 166, 0, 0.76);
  border: 1px solid;
  box-shadow: 0px 10px 15px -3px rgba(255, 255, 255, 0.123),0px 10px 15px -3px rgba(252, 251, 251, 0.1);
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.card-pr{
  background-color: #0a0a0a40;
  padding: 20px;
  border-radius: 8px;
  margin-top: 10px;
  border: 1px solid var(--color);
}
.card-fa{
  margin-top: 10px;
  padding: 10px;
  border-radius: 8px;
  background-color: #0a0a0a40;
  border: 1px solid var(--color);
}
.tx-loop{
  color: orange;
  font-weight: bold;
  font-size: 18px;
}
.p-fill{
  background-color: orange;
  padding: 5px;
  margin-top: 5px;
  border-radius: 5px;
  color: #0a0a0afa;
  font-weight: bold;
}